
      function getAbsoluteUrl(relativeUrl) {
        const publicPath = __webpack_public_path__;

        let url = '';

        if (!publicPath || publicPath.indexOf('://') < 0) {
          url += window.location.protocol + '//' + window.location.host;
        }

        if (publicPath) {
          url += publicPath;
        } else {
          url += '/';
        }

        return url + relativeUrl;
      }

import OrganisationBox from '@/components/OrganisationBox'
import { createDbWorker } from 'sql.js-httpvfs';

const publicPath = process.env.NODE_ENV === 'production' ? '/' : '/';

const workerUrl = new URL(`${publicPath}sql.js-httpvfs/sqlite.worker.js`, ({ url: getAbsoluteUrl('src/views/ServiceMap.vue?vue&type=script&lang=js') }).url);

const wasmUrl = new URL(`${publicPath}sql.js-httpvfs/sql-wasm.wasm`, ({ url: getAbsoluteUrl('src/views/ServiceMap.vue?vue&type=script&lang=js') }).url);

export default {
  name: 'Map',
  components: {
    OrganisationBox
  },
  data() {
    return {
      result: undefined,
      categories: undefined,
      selectedCategory: null,
    };
  },
  async mounted() {
    this.worker = await createDbWorker(
      [
        {
          from: 'inline',
          config: {
            serverMode: 'full',
            url: `${publicPath}db/data.sqlite3`,
            requestChunkSize: 4096,
          },
        },
      ],
      workerUrl.toString(),
      wasmUrl.toString()
    );
    document.title = 'Service Mapping | Cyber Clinic';
    this.categories = await this.worker.db.query('SELECT * FROM category');
    this.getAllOrganisations();
  },
  methods: {
    async findOrganisationByCategory() {
      this.$store.commit('setIsLoading', true)
      var cat = document.getElementById('category-select').value;
      this.result = await this.worker.db.query('SELECT * FROM organisation INNER JOIN category_mapping ON category_mapping.cam_org_id = organisation.org_id INNER JOIN category ON category_mapping.cam_cat_id = category.cat_id WHERE category_mapping.cam_cat_id=?', [cat])
      this.$store.commit('setIsLoading', false)
      this.updateResultCount();
    },
    async getAllOrganisations() {
      this.result = await this.worker.db.query('SELECT * FROM organisation');
      this.updateResultCount();
    },
    updateResultCount() {
      const count = document.querySelector('#result-count');
      count.innerHTML = this.result.length;
    },
  },
};
